.skills {
    height: 70vh;
    background-color: #111;
    color: var(--text-light);
    padding: 15vh 5vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.skills h1 {
    margin-bottom: 100px;
    text-align: center;
}
 
.skills-item {
    display: flex;
    align-items: center;
    width: 60%;
    margin: auto;
    transition: .3s linear;
}

.skills-item h2 {
    margin: 20px;
    width: 200px;
    margin-right: 50px;
}

.progerss-bar {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.progress-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.progerss-bar .progress-line {
    position: relative;
    width: 100%;
    margin-top: 10px;
    background: #fff;
    height: 1px;
}

.progerss-bar .progress-line-res {
    content:' ';
    position: absolute;
    width: 34%;
    margin-top: -1px;
    background: #298fee;
    height: 3px;
}

.progerss-item {
    cursor: pointer;
    width: 80px;
    text-align: center;
}

.progerss-item::before {
    display: block;
    content: '';
    border-radius: 50%;
    position: absolute;
    margin-top: 25px;
    margin-left: 35px;
    border: 1px solid #eee;
    background: #222;
    width: 8px;
    height: 8px;
    z-index: 1;
}

.progerss-item:hover::before {
    background: #fff !important;
}

.progerss-bar .done::before {
    background: #298fee;
}

@media (max-width : 1000px) {
    .skills {
        height: auto;
    }
    .skills-item {
        display: block;
    }
}

@media (max-width : 500px) {
    .skills {
        padding: 15vh 0;
    }
    .skills h1 {
        margin-bottom: 20px;
    }
}